@value fontWeightStrong from '@/styles/typography.css';

.row {
    display: flex;
    flex-flow: row wrap;
    align-items:flex-end;
}

.row > div {
    padding: 10px;
}

.name {
    font-weight: fontWeightStrong;
    flex-basis: 100%;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.values {
    flex-grow: 1;
}

.values > div {
    line-height: 1.1em;
    padding: 10px 5px;
}

.buttons a:first-child {
    margin-right: 10px;
}

.desktop.row {
    align-items: center;
}

.desktop .name {
    flex-basis: 33%;
}

.desktop .values {
    display: flex;
    flex-grow: 2;
    text-align: right;
}

.desktop .values .value{
    flex-basis: 50%;
}

.desktop .values .precent{
    flex-basis: 50%;
}


