@value ( red50,
red100,
green50,
green100,
green150,
white50,
white100,
silver50) from '../colors.css';

@value ( fontSize,
btnFontSize) from '../typography.css';

@value ( btnPadding ) from '../spacing.css';

.widgetContainer {
    font-size: fontSize;
    background-color: white50;
    color: black50;
}

.even {}

.odd {
    background-color: silver50;
}

.btn {
    color: white50;
    font-size: btnFontSize;
    padding: btnPadding;
}

.btnPrimary {
    composes: btn from '../buttons.css';
    composes: btn;
    background-color: green150;
    border-color: green150;
}

.btnDanger {
    composes: btn from '../buttons.css';
    composes: btn;
    background-color: red100;
    border-color: red100;
}

.btnUp {
    composes: btnPrimary;
}

.btnUp:before {
    content: url(data:image/gif;base64,R0lGODlhCAAMAIABAP///////yH5BAEAAAEALAAAAAAIAAwAAAIUjAGmiMv9YoLpVBUaztdt/mmYWAAAOw==);
    margin-right: 5px;
}

.btnDown {
    composes: btnDanger;
}

.btnDown:before {
    content: url(data:image/gif;base64,R0lGODlhCAAMAIABAP///////yH5BAEAAAEALAAAAAAIAAwAAAITjAGmgXvpWoRLVtoy2pxxvE1KAQA7);
    margin-right: 5px;
}

.colorPositive {
    color: green50;
}

.colorNegative {
    color: red50;
}

.table {
    composes: table from '../table.css';
}

.tableRow {
    composes: row from '../table.css';
}


@keyframes tickPos {
    from {
        background-color: #a6cf9d;
    }

    to {
        background-color: rgba(166,207,157,0);
    }
}

@keyframes tickNeg {
    from {
        background-color: #f2a2a2;
    }

    to {
        background-color: rgba(242,162,162,0);
    }
}

.tickNeg {
    animation: tickNeg 1s 0s linear;
}

.tickPos {
    animation: tickPos 1s 0s linear;
}




